@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position:fixed;
  padding:0;
  margin:0;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
}

/*REPEATED CODE... NOT MY PROUDEST CSS*/

.bin {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50%;
  background: #FFFFFF;
}

.dec {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50%;
  background: linear-gradient(90deg, #36F17D 0%, #11998E 100%);
}

.bin-form {
  width: 275px;
}

.dec-form {
  width: 275px;
}

.binary-text {
  width: 100%;
  border: 0;

  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 64px;
  line-height: 78px;

  outline: none;
  color: #000000;
  background-color:transparent;
}

.decimal-text {
  width: 100%;
  border: 0;

  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 64px;
  line-height: 78px;

  outline: none;
  color: #FFFFFF;
  background-color:transparent;
}

.binary-line {
  width: 100%;
  border: 2px solid #000000;
}

.decimal-line {
  width: 100%;
  border: 2px solid #FFFFFF;
}

